import React from 'react';
import BigPanel from '../../common/bigpanel';
import Environment from '../../common/environment';
import Player from '../../common/player';

function WorldMapPage(props) {
    return (
        <div>
            <Environment
                moving = {false}
            />

            <Player
                isCenter = {false}
                isRunning = {false}
                invert = {true}
            />

            <BigPanel>
            </BigPanel>
               
            
        </div>
    );
}

export default WorldMapPage;